<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <div slot="header" class="clearfix">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-input placeholder="" v-model="queryInfo.keywords" clearable @clear="upgradeList">
              <el-button slot="append" icon="el-icon-search" @click="upgradeList"></el-button>
            </el-input>
          </el-col>
        </el-row>
      </div>
      <!--  -->
      <el-row>
        <el-button type="primary" icon="el-icon-plus" @click="addDialog" size="mini"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteClick()"></el-button>
      </el-row>

      <!--  -->
      <el-table :data="upgradeListData" border style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="version" label=""></el-table-column>
        <el-table-column prop="type" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.type === '1'"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="way" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.way === '0'"></span>
            <span v-else-if="scope.row.way === '1'"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | date }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip :enterable="false" effect="dark" content="" placement="top" v-if="scope.row.status === '1'">
              <el-button type="danger" icon="el-icon-remove-outline" size="mini" @click="statusEdit(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" effect="dark" content="" placement="top" v-else>
              <el-button type="success" icon="el-icon-circle-plus-outline" size="mini" @click="statusEdit(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" effect="dark" content="" placement="top">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" effect="dark" content="" placement="top">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteClick(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.current_page"
        :page-sizes="[25, 30, 40, 50]"
        :page-size="queryInfo.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>

      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addFormClose">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="title"><el-input placeholder="" v-model="addForm.title"></el-input></el-form-item>
          <el-form-item label="" prop="version"><el-input placeholder="" v-model="addForm.version"></el-input></el-form-item>
          <el-form-item label="" prop="type">
            <el-select v-model="addForm.type" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="way">
            <el-select v-model="addForm.way" placeholder="">
              <el-option label="" value="2"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="wgt_url">
            <!--  -->
            <el-upload class="avatar-uploader" :action="updateUrl" :headers="headers" :show-file-list="false" :on-success="handleAvatarSuccessAddFile" name="file">
              <img v-if="addForm.wgt_url" src="../../assets/image/zip.png" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="addForm.status" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="content"><el-input :rows="5" type="textarea" placeholder="" v-model="addForm.content"></el-input></el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitAdd"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editFormClose">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
          <el-form-item label="" prop="title"><el-input placeholder="" v-model="editForm.title"></el-input></el-form-item>
          <el-form-item label="" prop="version"><el-input placeholder="" v-model="editForm.version"></el-input></el-form-item>
          <el-form-item label="" prop="type">
            <el-select v-model="editForm.type" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="way">
            <el-select v-model="editForm.way" placeholder="">
              <el-option label="" value="2"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="wgt_url">
            <!--  -->
            <el-upload class="avatar-uploader" :action="updateUrl" :headers="headers" :show-file-list="false" :on-success="handleAvatarSuccessEditFile" name="file">
              <img v-if="editForm.wgt_url" src="../../assets/image/zip.png" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="content"><el-input :rows="5" type="textarea" placeholder="" v-model="editForm.content"></el-input></el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitEdit"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        //
        keywords: '',
        //
        current_page: 1,
        //
        per_page: 25,
        // 
        total: 0
      },
      // 
      upgradeListData: [],
      // ID
      deleteId: '',
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // URL
      updateUrl: window.serverConfig.BASE_API + 'base/upload',
      // 
      headers: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      addForm: {
        title: '',
        version: '',
        type: '',
        way: '',
        wgt_url: '',
        status: '',
        content: ''
      },
      // 
      addFormRules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        version: [{ required: true, message: '', trigger: 'blur' }],
        type: [{ required: true, message: '', trigger: 'change' }],
        way: [{ required: true, message: '', trigger: 'change' }],
        wgt_url: [{ required: true, message: '', trigger: 'change' }],
        status: [{ required: true, message: '', trigger: 'change' }],
        content: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editForm: {},
      // 
      editFormRules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        version: [{ required: true, message: '', trigger: 'blur' }],
        type: [{ required: true, message: '', trigger: 'change' }],
        way: [{ required: true, message: '', trigger: 'change' }],
        wgt_url: [{ required: true, message: '', trigger: 'change' }],
        content: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  created() {
    // 
    var self = this;
    document.onkeydown = function(e) {
      var key = window.event.keyCode;
      if (key === 13) {
        self.upgradeList();
      }
    };
    this.upgradeList();
  },
  methods: {
    /**
     * 
     */
    async upgradeList() {
      const { data: res } = await this.$http.get('upgrade/list', { params: this.queryInfo });
      if (res.code !== 200) return this.$message.error(res.msg);
      this.queryInfo.total = res.data.total;
      this.upgradeListData = res.data.data;
    },

    /**
     * 
     */
    addDialog() {
      // 
      this.addDialogVisible = true;
    },

    /**
     * 
     */
    submitAdd() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post('upgrade/add', this.addForm);
        if (res.code !== 201) return this.$message.error(res.msg);
        this.$message.success(res.msg);
        // 
        this.addDialogVisible = false;
        this.upgradeList();
      });
    },

    // 
    async editDialog(id) {
      const { data: res } = await this.$http.get(`upgrade/query/${id}`);
      if (res.code !== 200) return this.$message.error(res.msg);
      this.editForm = res.data;
      // 
      this.editDialogVisible = true;
    },

    /**
     * 
     */
    submitEdit() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.put('upgrade/edit', this.editForm);
        if (res.code !== 200) return this.$message.error(res.msg);
        this.$message.success(res.msg);
        // 
        this.editDialogVisible = false;
        // 
        this.upgradeList();
      });
    },

    /**
     * 
     */
    deleteClick(id = '') {
      if (id !== '') {
        this.deleteId = id;
      }
      if (this.deleteId === '' || this.deleteId === undefined || this.deleteId == null) {
        return this.$message.warning('');
      }
      // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          // 
          const { data: res } = await this.$http.delete('upgrade/delete', { params: { id: this.deleteId } });
          if (res.code !== 200) return this.$message.error(res.msg);
          this.$message.success(res.msg);
          // 
          this.upgradeList();
        })
        .catch(() => {
          this.$message.info('');
          // 
          this.upgradeList();
        });
    },

    /**
     * 
     */
    handleSelectionChange(val) {
      // 
      var idArray = [];
      val.forEach(item => {
        idArray.push(item.id);
      });
      // 
      this.deleteId = idArray.join(',');
    },

    /**
     * 
     * @param {Object} val
     */
    handleSizeChange(val) {
      this.queryInfo.per_page = val;
      // 
      this.upgradeList();
    },

    /**
     * 
     * @param {Object} val
     */
    handleCurrentChange(val) {
      this.queryInfo.current_page = val;
      // 
      this.upgradeList();
    },

    /**
     * 
     * @param {Object} row 
     */
    statusEdit(row) {
      // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          let status = '1';
          if (row.status === '1') {
            status = '0';
          }
          // 
          const { data: res } = await this.$http.put('upgrade/statusedit', { id: row.id, status: status });
          if (res.code !== 200) return this.$message.error(res.msg);
          this.$message.success(res.msg);
          // 
          this.upgradeList();
        })
        .catch(() => {
          this.$message.info('');
          // 
          this.upgradeList();
        });
    },

    /**
     * 
     */
    addFormClose() {
      // 
      this.$refs.addFormRef.resetFields();
    },

    /**
     * 
     */
    editFormClose() {
      // 
      this.$refs.editFormRef.resetFields();
    },

    /**
     * 
     * @param {Object} res 
     * @param {Object} file 
     */
    handleAvatarSuccessAddFile(res, file) {
      if (res.code !== 200) return this.$message.error(res.msg);
      this.addForm.wgt_url = res.data[0];
      this.$message.success(res.msg);
    },

    /**
     * 
     * @param {Object} res 
     * @param {Object} file 
     */
    handleAvatarSuccessEditFile(res, file) {
      if (res.code !== 200) return this.$message.error(res.msg);
      this.editForm.wgt_url = res.data[0];
      this.$message.success(res.msg);
    }
  }
};
</script>

<style lang="less" scoped></style>
